function BgBar() {
  const divStyle = {
    position: "absolute",
    inset: "0",
    height: "200px",
    backgroundColor: "#1cabe2",
    width: "100%",
    zIndex: "-10",
    textIndent: "-999999px",
  };
  const divStyleIn = {
    background:
      "url(data:image/png;base64,) no-repeat center right/contain",
    maxWidth: "1120px",
    margin: "0 auto",
    height: "200px",
    padding: "0 20px",
  };
  return (
    <>
      <div className="bgBar" style={divStyle}>
        <div style={divStyleIn}></div>
      </div>
    </>
  );
}

export default BgBar;
